import { Fragment, useEffect } from "react";
import "./MyOrders.css";
import { useSelector, useDispatch } from "react-redux";
import { DataGrid } from "@material-ui/data-grid";
import { useAlert } from "react-alert";
import Loader from "../../layout/Loader/Loader";
import MetaData from "../../layout/MetaData/MetaData";
import { Typography } from "@material-ui/core";
import { MdLaunch } from "react-icons/md";
import { clearErrors, myOrders } from "../../../redux/actions/orderAction";
import { userAuth } from "../../../util/userAuth";
import { useNavigate, Link } from "react-router-dom";

const MyOrders = () => {
  const dispatch = useDispatch();
  const alert = useAlert();
  const navigate = useNavigate();
  const {
    user,
    isAuthenticated,
    loading: userLoading,
  } = useSelector((state) => state.user);
  const { loading, error, orders } = useSelector((state) => state.myOrders);

  const columns = [
    {
      field: "id",
      headerName: "Order ID",
      minWidth: 300,
      flex: 0.5,
    },
    {
      field: "status",
      headerName: "Status",
      minWidth: 150,
      flex: 0.2,
      cellClassName: (params) => {
        return params.getValue(params.id, "status") === "Delivered"
          ? "greenColor"
          : "redColor";
      },
    },
    {
      field: "noOfItems",
      headerName: "No of Items",
      type: "number",
      minWidth: 150,
      flex: 0.2,
    },
    {
      field: "amount",
      headerName: "Amount",
      type: "number",
      minWidth: 150,
      flex: 0.3,
    },
    {
      field: "action",
      headerName: "Action",
      type: "number",
      minWidth: 150,
      flex: 0.2,
      sortable: false,
      renderCell: (params) => {
        return (
          <Link to={`/order/${params.getValue(params.id, "id")}`}>
            <MdLaunch size={window.innerWidth >= 600 ? 25 : 20} />
          </Link>
        );
      },
    },
  ];
  const rows = [];

  orders &&
    orders.forEach((order, index) => {
      rows.push({
        noOfItems: order.orderItems.length,
        id: order._id,
        status: order.orderStatus,
        amount: order.totalPrice,
      });
    });
  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }
    dispatch(myOrders());

    userAuth(isAuthenticated, navigate);
  }, [error, dispatch, alert, isAuthenticated, navigate]);
  return (
    <Fragment>
      {loading && userLoading ? (
        <Loader />
      ) : (
        <div className="myOrdersPage">
          <MetaData title={`${user.name} - Orders`} />
          <DataGrid
            rows={rows}
            columns={columns}
            pageSize={10}
            disableSelectionOnClick
            className="myOrdersTable"
            autoHeight
          />

          <Typography id="myOrdersHeading">{`${user.name}'s Orders`}</Typography>
        </div>
      )}
    </Fragment>
  );
};

export default MyOrders;
